import VueRouter from 'vue-router'

const Foo = { 
  template: '<div>foo {{$route.params.id}} {{$route.query.name}}</div>',
  watch: {
    '$route' (to, from) {
      window.alert([to.path, from.path])
    }
  }
}
const Bar = { template: `
  <div >
    <h2>bar start</h2>
    <router-link to='/bar/profile'>/bar {{$route.params.id}}</router-link><br/>
    <router-link to='/bar/redirectToBar'>/bar/redirectToBar</router-link><br/>
    <router-view></router-view><br/>
    <router-view name="suBar"></router-view>
    <h2>bar end</h2>
  </div>
  ` 
}
const BarBaby = { template: '<div>BarBaby</div>',
    beforeRouteEnter(to, from, next) {
        window.console.log(to.path+'  enter BarBaby '+from.path);
        next();
    }};
const Baraby = { template: '<div>Baraby</div>'};
const BarProfile = { template: '<div>hrhr</div>'};
let login = false;
const routes = [
  { path: '/foo/:id', component: Foo , alias: '/fooo/:id', beforeEnter: (to, from, next) => {
    window.console.log(to.matched.$route+' before '+to.params.id);
    if (to.params.id === 'hello') {
        next();
    } else {
        next(false);
    }
  }},
  { path: '/bar', components: { default: Bar, subBar: BarBaby },meta: { requiresAuth: true},
  children: [
    { path: 'redirectToBar', redirect: 'profile'},
    { path: 'profile', components: { 
      default: BarProfile,
      suBar: Baraby
    }}
  ], beforeEnter: (to, from, next) => {
    if (to.matched.some(ele => ele.meta.requiresAuth)) {
        if (!login) {
            window.console.log("logout: ", login)
            login = true;
            window.console.log("login: ", login)
            next(false);
        } else {
            window.console.log("login: ", login)
            next();
        }
      } else {
        window.console.log("login: ", login)
        next();
      }
  }, watch: {
      '$route' (to, from) {
          if (this.$route.params.id === 'profile') {
              window.console.log(to.path+' watch '+from.path)
          }
      }
  }}
]

// 3. Create the router instance and pass the `routes` option
// You can pass in additional options here, but let's
// keep it simple for now.
const router = new VueRouter({
  routes
})
/* router.afterEach((to, from) => {
    window.console.log(to.path+' after '+from.path);
});
router.beforeResolve((to, from, next) => {
    window.console.log(to.path+' resolve '+from.path);
    next();
});
router.beforeEach((to, from, next) => {
    window.console.log(to.path+' before '+from.path);
    next();
}); */

export default router;
